"use client"

import { useState, useEffect } from "react"
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Loader2, CreditCard, DollarSign, Check, Star } from "lucide-react"
import { loadStripe } from "@stripe/stripe-js"
import { Elements } from "@stripe/react-stripe-js"
import { PayPalScriptProvider } from "@paypal/react-paypal-js"
import { StripeForm } from "./payment/stripe-form"
import { PayPalPaymentButton } from "./payment/paypal-button"
import { toast } from "sonner"
import { cn } from "@/lib/utils"

interface Plan {
  _id: string
  name: string
  key: string
  price: number
  credits: number
  features: string[]
  isPopular?: boolean
}

interface PlanUpgradeModalProps {
  isOpen: boolean
  onClose: () => void
  onSuccess: () => void
  initialPlan?: Plan
}

export function PlanUpgradeModal({ isOpen, onClose, onSuccess, initialPlan }: PlanUpgradeModalProps) {
  const [isProcessing, setIsProcessing] = useState(false)
  const [loading, setLoading] = useState(true)
  const [settings, setSettings] = useState<any>(null)
  const [plans, setPlans] = useState<Plan[]>([])
  const [selectedPlan, setSelectedPlan] = useState<Plan | null>(initialPlan || null)
  const [clientSecret, setClientSecret] = useState("")
  const [transactionId, setTransactionId] = useState("")
  const [stripePromise, setStripePromise] = useState<any>(null)

  useEffect(() => {
    if (isOpen && initialPlan) {
      setSelectedPlan(initialPlan)
    }
  }, [isOpen, initialPlan])

  useEffect(() => {
    if (isOpen) {
      const fetchData = async () => {
        try {
          const [settingsRes, plansRes] = await Promise.all([
            fetch("/api/settings/public"),
            fetch("/api/plans")
          ])
          
          const settingsData = await settingsRes.json()
          const plansData = await plansRes.json()
          
          setSettings(settingsData)
          setPlans(plansData.plans || [])
          
          if (settingsData.payment.stripePublicKey) {
            setStripePromise(loadStripe(settingsData.payment.stripePublicKey))
          }
        } catch (error) {
          console.error("Failed to load data", error)
          toast.error("Failed to load subscription options")
        } finally {
          setLoading(false)
        }
      }
      fetchData()
    }
  }, [isOpen])

  const initStripePayment = async () => {
    if (!selectedPlan) return
    setIsProcessing(true)
    try {
      const res = await fetch("/api/payments/create", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          provider: "stripe",
          amount: selectedPlan.price,
          credits: selectedPlan.credits,
        }),
      })
      const data = await res.json()
      if (data.error) throw new Error(data.error)
      
      setClientSecret(data.clientSecret)
      setTransactionId(data.transactionId)
      
      await createPlanUpgradeTransaction("stripe")
    } catch (error: any) {
      toast.error(error.message)
    } finally {
      setIsProcessing(false)
    }
  }

  const createPlanUpgradeTransaction = async (provider: string) => {
    if (!selectedPlan) return
    const res = await fetch("/api/user/upgrade-plan", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ 
        plan: selectedPlan.key,
        paymentMethod: provider,
        amount: selectedPlan.price
      }),
    })
    const data = await res.json()
    if (!res.ok) throw new Error(data.error)
    return data.transactionId
  }

  const handleCODUpgrade = async () => {
    setIsProcessing(true)
    try {
      await createPlanUpgradeTransaction("cod")
      toast.success("Upgrade request submitted! Waiting for admin approval.")
      onSuccess()
      onClose()
    } catch (error: any) {
      toast.error(error.message || "Failed to submit upgrade request")
    } finally {
      setIsProcessing(false)
    }
  }

  if (loading) return null

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className={cn("max-w-[900px] p-0 overflow-hidden", selectedPlan ? "sm:max-w-[600px]" : "sm:max-w-[900px]")}>
        {!selectedPlan ? (
          <div className="p-6">
            <DialogHeader className="mb-6 text-center">
              <DialogTitle className="text-3xl font-bold">Choose Your Plan</DialogTitle>
              <DialogDescription className="text-lg">
                Unlock more power with our premium plans
              </DialogDescription>
            </DialogHeader>
            
            <div className="grid gap-6 md:grid-cols-3">
              {plans.map((plan) => (
                <div 
                  key={plan._id} 
                  className={cn(
                    "relative flex flex-col rounded-xl border-2 p-6 transition-all hover:border-primary/50 hover:shadow-lg",
                    plan.isPopular ? "border-primary bg-primary/5" : "border-border"
                  )}
                >
                  {plan.isPopular && (
                    <div className="absolute -top-3 left-1/2 -translate-x-1/2 rounded-full bg-primary px-3 py-1 text-xs font-bold text-primary-foreground">
                      MOST POPULAR
                    </div>
                  )}
                  <div className="mb-4">
                    <h3 className="text-xl font-bold capitalize">{plan.name}</h3>
                    <div className="mt-2 flex items-baseline">
                      <span className="text-3xl font-bold">${plan.price}</span>
                      <span className="text-sm text-muted-foreground">/month</span>
                    </div>
                  </div>
                  <ul className="mb-6 flex-1 space-y-2">
                    <li className="flex items-center text-sm">
                      <Star className="mr-2 h-4 w-4 text-primary" />
                      {plan.credits} Credits
                    </li>
                    {plan.features.map((feature, i) => (
                      <li key={i} className="flex items-center text-sm">
                        <Check className="mr-2 h-4 w-4 text-green-500" />
                        {feature}
                      </li>
                    ))}
                  </ul>
                  <Button onClick={() => setSelectedPlan(plan)} className="w-full" variant={plan.isPopular ? "default" : "outline"}>
                    Choose {plan.name}
                  </Button>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <div className="p-6">
             <DialogHeader className="mb-6">
              <Button variant="ghost" size="sm" className="absolute left-4 top-4" onClick={() => setSelectedPlan(null)}>
                ‚Üê Back
              </Button>
              <DialogTitle>Checkout - {selectedPlan.name}</DialogTitle>
              <DialogDescription>
                Complete your subscription to {selectedPlan.name} plan
              </DialogDescription>
            </DialogHeader>

            <div className="mb-6 rounded-lg border bg-muted/50 p-4">
              <div className="flex items-center justify-between">
                <span className="font-medium">Total Amount</span>
                <span className="text-2xl font-bold">${selectedPlan.price}</span>
              </div>
            </div>

            <Tabs defaultValue={settings?.payment?.isStripeEnabled ? "stripe" : settings?.payment?.isPaypalEnabled ? "paypal" : "cod"} className="w-full">
              <TabsList className="grid w-full grid-cols-3">
                <TabsTrigger value="stripe" disabled={!settings?.payment?.isStripeEnabled}>Stripe</TabsTrigger>
                <TabsTrigger value="paypal" disabled={!settings?.payment?.isPaypalEnabled}>PayPal</TabsTrigger>
                <TabsTrigger value="cod" disabled={!settings?.payment?.isCodEnabled}>COD</TabsTrigger>
              </TabsList>
              
              <TabsContent value="stripe" className="mt-4">
                {clientSecret ? (
                  <Elements stripe={stripePromise} options={{ clientSecret }}>
                    <StripeForm 
                      clientSecret={clientSecret} 
                      transactionId={transactionId} 
                      onSuccess={() => {
                        onSuccess()
                        onClose()
                      }} 
                    />
                  </Elements>
                ) : (
                  <Button onClick={initStripePayment} disabled={isProcessing} className="w-full">
                    {isProcessing ? (
                      <>
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        Processing...
                      </>
                    ) : (
                      <>
                        <CreditCard className="mr-2 h-4 w-4" />
                        Pay with Stripe
                      </>
                    )}
                  </Button>
                )}
              </TabsContent>
              
              <TabsContent value="paypal" className="mt-4">
                {settings?.payment?.paypalClientId && (
                  <PayPalScriptProvider options={{ clientId: settings.payment.paypalClientId, currency: settings.payment.currency }}>
                    <PayPalPaymentButton 
                      amount={selectedPlan.price} 
                      credits={selectedPlan.credits}
                      onSuccess={() => {
                        onSuccess()
                        onClose()
                      }} 
                    />
                  </PayPalScriptProvider>
                )}
              </TabsContent>

              <TabsContent value="cod" className="mt-4">
                <div className="space-y-4">
                  <div className="rounded-md bg-blue-50 p-4 text-blue-900 dark:bg-blue-900/20 dark:text-blue-100">
                    <p className="text-sm">
                      <strong>Note:</strong> Your plan will be upgraded manually after the admin verifies your payment.
                    </p>
                  </div>
                  <Button onClick={handleCODUpgrade} disabled={isProcessing} className="w-full">
                    {isProcessing ? (
                      <>
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        Processing...
                      </>
                    ) : (
                      <>
                        <DollarSign className="mr-2 h-4 w-4" />
                        Submit Request
                      </>
                    )}
                  </Button>
                </div>
              </TabsContent>
            </Tabs>
          </div>
        )}
      </DialogContent>
    </Dialog>
  )
}
